@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);*{margin:0;padding:0;border:0;outline:0;box-sizing:border-box;list-style:none;text-decoration:none}:root{--color-background:#3B3B3B;--color-background-variant:#4F6E8F;--color-primary:#BD8F41;--color-primary-variant:#7A6849;--color-white:#fff;--color-light:rgba(255, 255, 255, 0.6);--transition:all 400ms ease;--container-width-large:75%;--container-width-medium:86%;--container-width-small:90%}html{scroll-behavior:smooth}::-webkit-scrollbar{display:none}body{font-family:Poppins,sans-serif;background:var(--color-background);color:var(--color-white);line-height:1.7;background-image:url(../src/assets/background.jpg);background-size:cover;background-position:center;background-attachment:fixed;height:100%}.container{width:var(--container-width-large);margin:0 auto}h1,h2,h3,h4,h5{font-weight:500}h1{font-size:2.5rem}section{padding-top:8rem}section>h2,section>h5{text-align:center;color:var(--color-white)}section>h2{color:var(--color-primary);margin-bottom:3rem}.text-light{color:var(--color-light);transition:var(--color-light)}a{color:var(--color-primary);transition:var(--transition)}a:hover{color:var(--color-white)}.btn{width:max-content;display:inline-block;columns:var(--color-primary);padding:.75rem 1.2rem;cursor:pointer;border:2px solid var(--color-primary);transition:var(--transition)}.btn:hover{background:var(--color-white);color:var(--color-background);border-color:transparent}.btn-primary{background:var(--color-primary);color:var(--color-background)}img{display:block;width:100%;object-fit:cover}@media screen and (max-width:1024px){.container{width:var(--container-width-medium)}section{padding-top:3rem}}@media screen and (max-width:600px){.container{width:var(--container-width-small)}section>h2{margin-bottom:2rem}}